.form {
    position: relative;
    z-index: 1;
    background: #FFFFFF;
    max-width: 360px;
    margin: 0 auto 100px;
    padding: 45px;
    text-align: center;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
  }
  
  .form input {
    font-family: "Roboto", sans-serif;
    outline: 0;
    background: #f2f2f2;
    width: 100%;
    border: 0;
    margin: 0 0 15px;
    padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
  }
  
  .login-page .form h1 {
    font-family: Poppins;
  }
  
  .form button {
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    outline: 0;
    background: #1C3879;
    width: 100%;
    border: 0;
    padding: 15px;
    color: #FFFFFF;
    font-size: 14px;
    -webkit-transition: all 0.3 ease;
    transition: all 0.3 ease;
    cursor: pointer;
  }
  
  .form button:hover,
  .form button:active,
  .form button:focus {
    background: #4971ce;
  }
  
  .form .message {
    margin: 15px 0 0;
    color: #b3b3b3;
    font-size: 12px;
  }
  
  .form .message a {
    color: #2554c1;
    text-decoration: none;
  }
  .form .message a:hover {
    font-weight: bolder;
  }
  .form .register-form {
    display: none;
  }
  .login-page {
    width: 360px;
    padding: 30px 0 0;
    margin: auto;
  }
  .form label {
    display: inline-flex;
    align-items: center;
    margin-bottom: 0;
    font-size: 14px;
  }
  
  .form label input[type="checkbox"] {
    margin-right: 10px;
    width: 13px;
    margin-bottom: 0;
  }
  body{
    background: url(./pattern.svg);
    background-size: 2%;
  }

  @media only screen and (max-width: 1080px) {
    .form{
        width: 90%;
      }
  }